/* styles go here */
.form__field {
  display: block;
}

fieldset {
  margin-bottom: 20px;
}

input {
  margin-bottom: 10px;
}

fieldset:first-of-type {
  padding-bottom: 4px;
}

form fieldset:nth-of-type(3) {
  padding-bottom: 6px;
}

form fieldset:nth-of-type(2),
form fieldset:nth-of-type(4) {
  padding-bottom: 0;
}

.select__field {
  margin-top: 10px;
  margin-bottom: 10px;
}

div.form__field {
  padding-bottom: 4px;
}
